import React,{useState} from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { withRouter } from "react-router";
import { CREATE_JOB_SHEET } from 'apiServices/action'
import Dropdown from "Component/Dropdown";
import Search from "Component/Search";
import { getStockTakeSummary,removeEntryModule } from "apiServices";
import SKUItem from "Component/SKUItem";
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import "./index.scss";
import {selectSku} from './services'
import { filterBySkuName, responseCheck } from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";
import PopUpUnfinished from 'Component/Modal/PopUpUnfinished'
import Discard from 'Component/Modal/PopUpDiscard'
import { useAdditionalOutbound, useDimension } from "../GeneralServices";

const AdditionalOutbound = (props) => {
  const dispatch = useDispatch();
  const story = useHistory()
  const {height,emptyItem} = useDimension(400)

  const {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    status,
    isLoading,
    outboundEntryData,
    outboundAddSku,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    next,
    setNext,
    area,
    areaId,
    setAreaId,
    search,
    setSearch,
    discard,
    setDiscard,
    isCheckedTotal
  } = useAdditionalOutbound({dispatch})
  if (outboundAddSku === false && outboundEntryData?.jobPost.list.length > 0 && next) props.history.push("/inventory-entry/additional-outbound/weight-entry")

   //search
   const [category, setCategory] = useState()
   const [sortBy, setSortBy] = useState()

  // Condition
  const isPlural = isCheckedTotal > 1 ? "s" : ""
  const noLength = summary?.length ? "flex-column" : "flex-row"
  const isReady = true;
  return (
    <div className="w-100  stock-take-summary">
      <Discard
          discard={discard}
          module={'Inventory Entry - Additional Outbound'}
          moduleEntry={outboundEntryData}
          close={() => setDiscard(false)}
          discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch,setDiscard })}
      />
      {/* <PopUpUnfinished
        discard={discard}
        addSku={outboundAddSku}
        moduleFirstLoad={moduleFirstLoad}
        moduleEntry={outboundEntryData}
        redirect={() => props.history.push("/inventory-entry/additional-outbound/weight-entry")}
        previousPage={() => story.goBack()}
        setDiscard={() => setDiscard(true)}
      /> */}
      <PopUp status={status} />
      <div className="px-3 pt-3 bg-white border-all radius-all d-md-flex dd-group">
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Select Area"
            placeholder='Select Area'
            options={area}
            onChangeDropdown={(selected) => setAreaId(selected)}
            selectedValue={areaId}
            showTitle={true}
          />
        </div>
      </div>

      <div className="pt-3 pb-0 pl-3 pr-3 bg-white mt-3 border-all radius-all mb-3">
        <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
          <div className="text LatoBold d-flex justify-content-between align-items-center sku-des">
            <div>
              Enter SKU or Description{" "}
              <span className="green">{`(${isCheckedTotal} Selected SKU${isPlural})`}</span>
            </div>
          </div>
        </div>
        <Search
          setSortBy={setSortBy}
          category={category}
          setCategory={setCategory}
          placeholder={"Enter SKU"}
          filter={false}
          searchHandler={(desc) =>
            areaId ? getStockTakeSummary({category, dispatch, desc, warehouseUuid, areaId, setModule: setSummary, transaction: 'additionalOutbound', module:localEntryData }, setSearch(desc)) : setAreaId('empty' )
          }
        />
        <div
          className={`mb-3 mt-3 d-flex align-items-center scroll-sku ${noLength} w-100 text-center`} style={{ minHeight: height , maxHeight: height}}>
          {summary?.length ? (
            summary?.sort( function(a,b){ return filterBySkuName(a,b,sortBy?.value)})?.map((data, idx) => {
              return (
                <SKUItem
                  key={idx}
                  img={data?.photo}
                  skuDescription={data?.skuName}
                  skuNumber={data?.skuNumber}
                  uomLabel="weight"
                  uomValue={data?.totalQty}
                  uom={data?.unitOfMeassures}
                  isCheckedHandler={(checked) =>
                    selectSku({
                      idx,
                      checked,
                      dispatch,
                      setLocalEntryData,
                      moduleSummary: summary,
                      moduleEntry: localEntryData,
                      moduleName,
                      warehouseUuid,
                      areaId,
                    })
                  }
                  check={data?.checked}
                />
              );
            })
          ) : (
              <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: emptyItem,maxHeight: emptyItem}}>
                {!responseCheck({ isLoading, summary }) ? (
                  <img className="loading" src={loading} alt="loading" />
                ) : areaId && area && summary?.length === 0 ? (
                  <div>
                    Sorry, we couldn't find any matches
                    for '<span className='LatoBold'>{search}</span>' in {areaId && areaId?.label} area.
                    <br/>
                    Please Try search with another term.
                  </div>
                ) : 'Enter SKU'}
              </div>
            )}
        </div>
      </div>
      <div className="h-screen-summary-footer d-flex flex-column justify-content-start">
        <div className="w-100 d-flex align-items-center justify-content-between mb-1">
          <button
            tabIndex='0'
            onClick={() => props.history.push("/inventory-entry/additional-outbound/CSV-upload")}
            type="button"
            className="btn sku-item px-4 no-shadow border-all bg-white fontSizeButton"
          >
            Upload File
          </button>
          <button
            tabIndex='0'
            onClick={() => {
              if (localEntryData) {
                setNext(true)
                dispatch({ type: CREATE_JOB_SHEET, outboundEntryData: localEntryData })
                props.history.push("/inventory-entry/additional-outbound/weight-entry")
              }
            }
            }
            type="button"
            className={`btn sku-item px-5 fontSizeButton no-shadow text-white ${localEntryData ? "bg-green" : "bg-lineGrey"
              }`}
          >

            {isReady ? 'Next' : 'Processing...'}

          </button>
        </div>
      </div>
    </div>
  );
};

export default withRouter(AdditionalOutbound);
